import { HttpClient } from '@angular/common/http';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import { bypassSanitizationTrustResourceUrl } from '@angular/core/src/sanitization/bypass';
import { DomSanitizer } from '@angular/platform-browser';

@Component({
  selector: 'app-upload-video',
  templateUrl: './upload-video.component.html',
  styleUrls: ['./upload-video.component.scss']
})
export class UploadVideoComponent implements OnInit {
  videoFormGroup = new FormGroup({
    video: new FormControl(null, [Validators.required])
  });
  videoFile = null;
  isSending = false;
  sent = false;
  error = false;
  @ViewChild('videoView') videoView: ElementRef;



  constructor(private http: HttpClient) { }

  ngOnInit() {
  }

  fileChangeEvent(event: any) {
    console.log('Evento', event);
    console.log('Form control video', this.videoFormGroup.get('video').value);
    const files = (event && event.target && event.target.files) ? event.target.files : [];


    if (files.length > 0) {
      this.videoFile = files[0];
      console.log('Video file', this.videoFile);
      this.videoView.nativeElement.src = URL.createObjectURL(this.videoFile);
      this.videoView.nativeElement.load();
    }
  }

  getLocalUrlFile() {
    if (this.videoFile) {
      return URL.createObjectURL(this.videoFile);
    }
    return null;
  }

  saveVideo() {
    const formData = new FormData();
    const date = new Date();
    const fileName = '' +
    date.getDay() +
    date.getMonth() +
    date.getFullYear() +
    date.getMinutes() +
    date.getSeconds() + date.getMilliseconds();

    formData.append('video', this.videoFile, fileName);
    this.isSending = true;

    this.http.post('http://localhost:3333/promotion/video', formData).subscribe(data => {
      console.log('Data', data);
      this.isSending = false;
      this.error = false;
      this.sent = true;
    },
      err => {
        this.isSending = false;
        this.sent = false;
        this.error = true;
      }
    );

  }

}
