import { Promotion } from './../../../models/promotion.model';
import { HttpClient, HttpParams } from '@angular/common/http';
import { Component, OnInit, OnDestroy, AfterViewInit, Input, OnChanges } from '@angular/core';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-edit-promotions',
  templateUrl: './edit-promotions.component.html',
  styleUrls: ['./edit-promotions.component.scss']
})
export class EditPromotionsComponent implements OnInit, OnDestroy, OnChanges {
  @Input()
  selected: number;

  promotionsSubscription: Observable<any> = null;
  promotions: Promotion[] = [];
  baseUrl = 'http://localhost:3333';

  constructor(private http: HttpClient) { }

  ngOnInit() {
    console.log('NgOnInit()');
    this._loadElements();
  }

  private _loadElements() {
    this.promotions = [];
    this.http.get(this.baseUrl + '/promotion').subscribe((data: Promotion[]) => {
      if (data) {
        this.promotions = data;
      }
    });
  }

  ngOnDestroy() {
    // TODO: falta arreglar esta parte
    console.log('ngOnDestroy()');
  }

  ngOnChanges() {
    // Si es la segunda pesta√±a volvemos a cargar
    if (this.selected === 2) {
      this._loadElements();
    }
  }

  deleteOne(id: string) {
    console.log('Deleting...', id);
    const params = new HttpParams();
    params.append('_id', id);
    this.http.delete(this.baseUrl + '/promotion?id=' + id).subscribe(data => {
      this._loadElements();
    },
      err => {
        console.log('Error', err);
      }
    );
  }


  changeVisibility(id: string, visibility: boolean) {
    const resultado = this.promotions.find(element => element._id === id);
    if (resultado) {
      resultado.visible = visibility;
      this.http.put(this.baseUrl + '/promotion?id=' + id, resultado).subscribe(data => {
        this._loadElements();
      });
    }
  }

  hide(id: string) {
    this.changeVisibility(id, false);
  }

  show(id: string) {
    this.changeVisibility(id, true);
  }


}
