import { Promotion } from './../../../models/promotion.model';
import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormGroup, Validators, FormControl } from '@angular/forms';
import { ImageCroppedEvent } from 'ngx-image-cropper';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-upload-image',
  templateUrl: './upload-image.component.html',
  styleUrls: ['./upload-image.component.scss']
})
export class UploadImageComponent implements OnInit {
  uploadImageForm = new FormGroup({
    image: new FormControl(null, [Validators.required])
  });
  imageChangedEvent: any = '';
  croppedImage: any = '';
  imageBlob: Blob = null;
  formData = new FormData();
  isSending = false;
  sent = false;
  error = false;



  constructor(private http: HttpClient) { }

  ngOnInit() {
  }

  fileChangeEvent(event: any): void {
    console.log('Event', event);
    this.imageChangedEvent = event;
  }

  imageCropped(event: ImageCroppedEvent) {
    this.croppedImage = event.file;
    console.log('ImagedCroped', this.croppedImage);
  }
  imageLoaded() {
    // show cropper
  }
  cropperReady() {
    // cropper ready
  }
  loadImageFailed() {
    // show message
  }

  prepareImage() {
    const arrayBlob = new Array<Blob>();
    arrayBlob.push(this.croppedImage);
    const date = new Date();
    const fileName = '' +
      date.getDay() +
      date.getMonth() +
      date.getFullYear() +
      date.getMinutes() +
      date.getSeconds() + date.getMilliseconds();
    const file = new File(arrayBlob, `${fileName}.jpg`, { type: 'image/jpeg' });
    this.formData.append('image', file);
  }

  postImage() {
    this.isSending = true;
    this.http.post('http://localhost:3333/promotion/image', this.formData).subscribe((data: any) => {
      this.isSending = false;
      this.error = false;
      if (data) {
        this.sent = true;
        // Soluci√≥n a volver a subir imagen
        this.formData = new FormData();
        setTimeout(() => {
          this.sent = false;
        }, 2000);
      }
    },
    err => {
      this.isSending = false;
      this.sent = false;
      this.error = true;
      console.error('Error', err);
    });
  }

  uploadImage() {
    this.prepareImage();
    if (this.formData.get('image')) {
      this.postImage();
    }
  }

}
